import {getToken} from '@/utils/auth'
/**
 * api
 */
const apiLink = {
	// 
	AuthLoginByWeixin111: {url: '/pl/login ',text: ''},
	// h5
	AuthLoginByWeixin: {url: '/pl/login',text: 'h5'},
	getCode:{url:'/pl/vaildation',text:''},
	reg:{url:'',text:''},
	resetpwd:{url:'/pl/phReset',text:""},
	// 
	getUserinfo:{url:'/api/tzUserApi/persInformation',text:''},
	AddressList:{url:'/api/userAddr/addrList',text:''},
	Addressdetail:{url:'/api/userAddr/addrById',text:''},
	AddressSave:{url:'/api/userAddr/addrSave',text:''},
	AddressDelete:{url:'/api/userAddr/addrDelete',text:''},
	
	// 
	calendarList:{url:'/api/calebdarwork/list',text:''},
	calebdarInsert:{url:'/api/calebdarwork/calebdarInsert',text:''},
	calendarUpdate:{url:'/api/calebdarwork/calebdarUpdate',text:''},
}

const utils = {
	//  + 'wechat/'
	// httpUrl: 'http://127.0.0.1:8090/',




	httpUrl: 'http://192.168.1.61:8090',

	//
	interfaceUrl: function() {
		return utils.httpUrl
	},
	delayed: null,
	/**
	 * 
	 * @param string url 
	 * @param {*} postData 
	 * @param string method 
	 *  GET or POST
	 * @param string contentType 
	 *  'application/x-www-form-urlencoded'
	 *  'application/json'
	 * @param bool isDelay loading
	 * @param bool hideLoading loading
	 *  true: 
	 *  false:
	 */
	request: function(httpurl, postData = {}, method , contentType = "application/x-www-form-urlencoded", isDelay,
		hideLoading) {
		//
		let loadding = false;
		utils.delayed && uni.hideLoading();
		clearTimeout(utils.delayed);
		utils.delayed = null;
		if (!hideLoading) {
			utils.delayed = setTimeout(() => {
				uni.showLoading({
					mask: true,
					title: '...',
					success(res) {
						loadding = true
					}
				})
			}, isDelay ? 1000 : 0)
		}

		return new Promise((resolve, reject) => {
			
			uni.request({
				url: utils.interfaceUrl() + httpurl.url,
				data: postData,
				header: {
					// 'content-type': contentType,
					'Authorization': getToken()
				},
				method: method, //'GET','POST'
				dataType: 'json',
				success: (res) => {
					console.log(`${httpurl.text}+${utils.interfaceUrl()} ${httpurl.url}+${JSON.stringify(postData)}`)
					console.log(res.data)
					if (loadding && !hideLoading) {
						uni.hideLoading()
					}
					if (res.statusCode === 200) {
						if (res.data.errno === 401) {
							//401token
							
						} else if (res.data.errno === 500) {
							utils.toast(res.data.msg)
						} else if (res.data.errno === 404) {
							utils.toast(res.data.msg)
						}else if(res.data.statusCode===-10001){
							reject(res.msg)
							uni.removeStorage({
								key: 'token',
								success:()=>{
									//
									console.log(uni.getStorageSync("navUrl"))
									// if (uni.getStorageSync("navUrl") != "pages/index/index") {





















										uni.navigateTo({
											url:'/pages/common/wxlogin/wxlogin'
										})

									// }
								}
							})
						}
						 else {
							resolve(res.data);
						}
					} else {
						reject(res.data);
					}
				},
				fail: (res) => {
					utils.toast("~")
					reject(res)
				},
				complete: function(res) {
					clearTimeout(utils.delayed)
					utils.delayed = null;
					if (res.statusCode === 200) {
						if (res.data.errno === 0 || res.data.errno === 401) {
							uni.hideLoading()
						} else {
							uni.hideLoading()
							// utils.toast(res.data.msg)
						}
					} else {
						utils.toast('~')
					}
				}
			})
		})
	},
	toast: function(text, duration, success) {
		uni.showToast({
			title: text || "~",
			icon: success || 'none',
			duration: duration || 2000
		})
	},
	modal:function(title,content,showCancel=false,callback,confirmColor, confirmText, cancelColor, cancelText){
		uni.showModal({
			title: title || '',
			content: content,
			showCancel: showCancel,
			cancelColor: cancelColor || "#555",
			confirmColor: confirmColor || "#e41f19",
			confirmText: confirmText || "",
			cancelText: cancelText || "",
			success(res) {
				if (res.confirm) {
					callback && callback(true)
				} else {
					callback && callback(false)
				}
			}
		})
	},

	/**
	 * 
	 */
	login: function() {
		return new Promise(function(resolve, reject) {
			uni.login({
				success: function(res) {
					if (res.code) {
						resolve(res);
					} else {
						reject(res);
					}
				},
				fail: function(err) {
					reject(err);
				}
			});
		});
	},

	// code
	loginByWeixin: function(userInfo) {
		let code = null;
		return new Promise(function(resolve, reject) {
			return utils.login().then((res) => {
				code = res.code;
				return userInfo;
			}).then((userInfo) => {
				//
				console.log('code:' + code)
				console.log(userInfo)
				uni.setStorageSync('userInfo', userInfo)
				utils.request(apiLink.AuthLoginByWeixin111, {
					type:'wx',
					code: code,
					userInfo: userInfo
				}, 'POST', 'application/json').then(res => {
					console.log(res)
					if (res.statusCode === 1) {
						uni.setStorageSync('token', res.content)
						uni.setStorageSync('userId', res.userId)
						resolve(res)
					} else {
						utils.toast(res.errMsg)
						reject(res)
					}

					// if (res.errno === 0) {
					// 	//
					// 	uni.setStorageSync('userInfo', res.data.userInfo);
					// 	uni.setStorageSync('X-Nideshop-Token', res.data.token);

					// 	resolve(res);
					// } else {
					// 	utils.toast(res.errmsg)
					// 	reject(res);
					// }
				}).catch((err) => {
					reject(err);
				});
			}).catch((err) => {
				reject(err);
			})
		});
	},
	/**
	 * 
	 */
	getCode(seconds,phone){
		return new Promise((resolve,reject)=>{
			utils.request(apiLink.getCode,{
				phone
			},'POST','application/json').then((res)=>{
				if(res.statusCode===1){
					resolve(res)
				}else{
					reject(res.msg)
				}
			})
		})
	},
	/**
	 * 
	 */
	handleLogin(params){
		return new Promise((resolve,reject)=>{
			utils.request(apiLink.AuthLoginByWeixin,params,'POST','application/json').then((res)=>{
				resolve(res)
			}).catch((err)=>{
				reject(err.msg)
			})
		})
	},
	/**
	 * 
	 */
	handleAdress(newData){
		return new Promise((resolve,reject)=>{
			utils.request(apiLink.AddressSave,newData,'POST','application/json').then((res)=>{
				console.log(res)
				if(res.code===200){
					resolve(res)
				}
			}).catch((err)=>{
				reject(err.msg)
			})
		})
	}
}
module.exports = {
	apiLink: apiLink,
	interfaceUrl: utils.interfaceUrl,
	request: utils.request,
	getToken: utils.getToken,
	toast: utils.toast,
	modal:utils.modal,
	login: utils.login,
	loginByWeixin: utils.loginByWeixin,
	getCode:utils.getCode,
	handleLogin:utils.handleLogin,
	handleAdress:utils.handleAdress
}
